$black: #2a2a24;

* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
  font-size: 14px;
}

#root {
  overflow: auto;
}

body {
  position: fixed;
  overflow: hidden;
  overscroll-behavior-y: none;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir,
    helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  color: black;
  -webkit-font-smoothing: antialiased;
  background: #f1f4f8;
}

// header

header {
  position: absolute;
  min-height: auto;
  margin-top: 3vh;
  width: 100%;
  height: 100%;
  padding: 3vh 48px;
  align-items: center;
  text-align: center;
  color: $black;
  overflow-wrap: break-word;
  .logo {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  .logo,
  span {
    font-size: 6vh;
    font-weight: 700;
    letter-spacing: 0.4rem;
    align-items: center;
    font-family: "Cairo", sans-serif;
    font-weight: 600;
    text-align: center;
    justify-content: center;
  }
  .details {
    font-size: 2.6vh;
    min-width: fit-content;
    min-height: auto;
  }
}

$small: 300px;
$medium: 900px;

header {
  position: absolute;
  min-height: 30%;
  margin-top: 8rem;
  width: 100%;
  height: 100%;
  padding: 3vh 48px;
  align-items: center;
  text-align: center;
  color: $black;
  overflow-wrap: break-word;

  span {
    font-size: 3rem;
    font-weight: 700;
    letter-spacing: 0.4rem;
    align-items: center;
    font-family: "Cairo", sans-serif;
    font-weight: 600;
    text-align: center;
    justify-content: center;
  }
  .details {
    font-size: 1.5rem;
    min-width: fit-content;
    min-height: auto;
  }
}
